header {
  padding: 50px;
  background-image: url("../img/bg_1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 400px;
  h2 {
    font-size: 1.2rem;
  }

  form {
    margin-top: 50px;
    max-width: 400px;
  }

}

.card {
  margin: 25px;
}

.loading-element {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  display: none;
  &.loading {
    display: block;
  }
}
